      /* Search bar */
      .searchPageMain {
        border: 0;
        box-sizing: border-box;

        --dur: 1s;
        font-size: calc(16px + (24 - 16)*(100vw - 320px)/(1280 - 320));
      }

      .searchbarInput, body {
        font: 1em/1.5 Hind, sans-serif;
      }

      .searchbarInput, .searchForm, .caret {
        margin: auto;
      }

      .searchForm {
        position: relative;
        width: 100%;
        max-width: 17em;
      }

      .searchbarInput, .caret {
        display: block;
        transition: all calc(var(--dur) * 0.5) linear;
      }

      .searchbarInput {
        background: transparent;
        border-radius: 50%;
        box-shadow: 0 0 0 0.25em inset;
        caret-color: var(#79527f);
        width: 2em;
        height: 2em;
        -webkit-appearance: none;
        appearance: none;
      }

      .searchbarInput:focus, .searchbarInput:valid {
        background: var(--input);
        border-radius: 0.25em;
        box-shadow: none;
        padding: 0.75em 1em;
        transition-duration: calc(var(--dur) * 0.25);
        transition-delay: calc(var(--dur) * 0.25);
        width: 100%;
        height: 3em;
      }

      .searchbarInput:focus {
        -webkit-animation: showCaret var(--dur) steps(1);
                animation: showCaret var(--dur) steps(1);
        outline: transparent;
      }

      .searchbarInput:focus + .caret, .searchbarInput:valid + .caret {
        -webkit-animation: handleToCaret var(--dur) linear;
                animation: handleToCaret var(--dur) linear;
        background: transparent;
        width: 1px;
        height: 1.5em;
        -webkit-transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
                transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
      }

      .searchbarInput::-webkit-search-decoration {
        -webkit-appearance: none;
      }

      .searchbar-label {
        color: #79527f;
        overflow: hidden;
        position: absolute;
        width: 0;
        height: 0;
      }

      .caret {
        background: currentColor;
        border-radius: 0 0 0.125em 0.125em;
        margin-bottom: -0.6em;
        width: 0.25em;
        height: 1em;
        -webkit-transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
                transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
        -webkit-transform-origin: 50% 0;
                transform-origin: 50% 0;
      }
      
      /* Dark mode */
      @media (prefers-color-scheme: dark) {	
        :root {
          --bg: #17181c;
          --fg: #79527f;
          --input: #2e3138;
          --primary: #79527f;
        }
      }
      
      /* Animations */
      @-webkit-keyframes showCaret {
        from {
          caret-color: transparent;
        }
        to {
          caret-color: var(--primary);
        }
      }
      @keyframes showCaret {
        from {
          caret-color: transparent;
        }
        to {
          caret-color: var(--primary);
        }
      }
      @-webkit-keyframes handleToCaret {
        from {
          background: currentColor;
          width: 0.25em;
          height: 1em;
          -webkit-transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
                  transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
        }
        25% {
          background: currentColor;
          width: 0.25em;
          height: 1em;
          -webkit-transform: translate(0,-1em) rotate(-180deg) translate(0,0.875em);
                  transform: translate(0,-1em) rotate(-180deg) translate(0,0.875em);
        }
        50%, 62.5% {
          background: var(--primary);
          width: 1px;
          height: 1.5em;
          -webkit-transform: translate(0,-1em) rotate(-180deg) translate(7.5em,2.5em);
                  transform: translate(0,-1em) rotate(-180deg) translate(7.5em,2.5em);
        }
        75%, 99% {
          background: var(--primary);
          width: 1px;
          height: 1.5em;
          -webkit-transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
                  transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
        }
        87.5% {
          background: var(--primary);
          width: 1px;
          height: 1.5em;
          -webkit-transform: translate(0,-1em) rotate(-180deg) translate(7.5em,0.125em);
                  transform: translate(0,-1em) rotate(-180deg) translate(7.5em,0.125em);
        }
        to {
          background: transparent;
          width: 1px;
          height: 1.5em;
          -webkit-transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
                  transform: translate(0,-1em) rotate(-180deg) translate(7.5em,-0.25em);
        }
      }
      @keyframes handleToCaret {
        from {
          background: currentColor;
          width: 0.25em;
          height: 1em;
          -webkit-transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
                  transform: translate(0,-1em) rotate(-45deg) translate(0,0.875em);
        }
        25% {
          background: currentColor;
          width: 0.25em;
          height: 1em;
          -webkit-transform: translate(0,-1em) rotate(-180deg) translate(0,0.875em);
                  transform: translate(0,-1em) rotate(-180deg) translate(0,0.875em);
        }
        50%, 62.5% {
          background: var(--primary);
